@font-face {
	font-family: 'icomoon';
	src: url('../iconFonts/icomoon.eot?ydiwhd');
	src: url('../iconFonts/icomoon.eot?ydiwhd#iefix')
			format('embedded-opentype'),
		url('../iconFonts/icomoon.ttf?ydiwhd') format('truetype'),
		url('../iconFonts/icomoon.woff?ydiwhd') format('woff'),
		url('../iconFonts/icomoon.svg?ydiwhd#icomoon') format('svg');
	font-weight: normal;
	font-style: normal;
	font-display: block;
}

[class^='icon-'],
[class*=' icon-'] {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'icomoon' !important;
	speak: never;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

